import React from 'react';
import { Route, IndexRoute } from 'react-router';
import App from './containers/App';
import NotePage from './components/NotePage';
import NotesList from './components/NotesList';
import Page from './components/Page';


// to handle the authentication in one place, you can use HOC
import {browserHistory} from 'react-router';

const CheckAuth = (isLogined, redirectPath) => (Component) => class CheckAuth extends React.Component {
    componentDidMount(){
        this.checkAuth(nextProps);

    }
    componentWillMount(){
        //Check auth on enter
        console.log(this.props);
        this.checkAuth(this.props);
    }
    checkAuth({location}){
        if(!isLogined && location.pathname!==redirectPath) {
            browserHistory.replace(redirectPath);
        }
    }
    render(){
        return (<Component {...this.props}/>);
    }
};

let logined = true;
// const func=(prevState, nextState, replace)=> {
//       if(!logined && nextState.location.pathname !== '/login') {
//           replace('/login');
//       }
//   }
const routes = (
    <Route path="/" component={CheckAuth(logined,'/login')(App)} >
        <IndexRoute components={{notes_list:NotesList}}/>
        <Route path="login" components={{note_page:Page}}/>
        <Route path="note/:note_id" components={{notes_list:NotesList ,note_page:NotePage }}/>

        <Route path="label/:label_name" components={{notes_list:NotesList}}/>

        <Route path="label/:label_name/note/:note_id" components={{notes_list:NotesList,note_page:NotePage }}/>
    </Route>
);

export default routes;


